.heyform-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;

  &,
  * {
    box-sizing: border-box;
  }

  .heyform-loading-icon {
    width: 84px;
    height: 84px;
    flex-shrink: 0;
    margin-bottom: 32px;
    text-align: center;

    svg {
      display: inline-block;
      width: 50px;
      height: 57px;
      opacity: 0.7;
      animation: breathing 2s infinite;
    }
  }

  .heyform-loading-text {
    display: flex;
    align-items: center;
    color: #333;
    font-size: 24px;
    max-width: 500px;
    text-align: center;
  }
}

.components-heyform {
  .components-external-link__icon {
    width: 1.4em;
    height: 1.4em;
    margin: -0.2em 0.1em 0px;
    vertical-align: middle;
    fill: currentcolor;
  }
}

@keyframes breathing {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
    opacity: 1;
  }

  100% {
    transform: scale(1);
  }
}
